class vuex {
	constructor(store) {
	    this.store = store;
	}
	//
	state(){
		return this.store.state;
	}
	//
	getters(){
		let t = this;
		const g = this.store.getters
		let keys = Object.keys(g);
		
		let k = keys.map((el,index)=>{
			let f = el.split('/');
			let tst = {}
			if(f.length==1){
				tst[el]=g[el]
			}else{
				
				tst[f[0]]={}
				tst[f[0]][f[1]] = g[el]
				
			}
			return tst
		})
		let rulst = {};
		k.forEach(el=>{
			rulst = {...rulst,...el}
		})
		return rulst;
	}
	commit(funName,arg){
		try{
			this.store.commit(funName,arg);
		}catch(e){
			console.error(":"+funName)
		}
	}
	actions(funName,arg){
		try{
			return this.store.dispatch(funName,arg);
		}catch(e){
			console.error(":"+funName)
		}
	}
	//vuex
	getVuex(){
		return this.store;
	}
}


export default vuex;