	getNowData(){
		//  
		let week = [7,1,2,3,4,5,6]
		let text_week = ['','','','','','','']
		let _thisdateStr = this.now_day_month.toLocaleString();
		let _thisMothn = this.now_day_month.getMonth();//
		let _thisDay = this.now_day_month.getDate();//
		let _thisYear = this.now_day_month.getFullYear();//
		let _thisWeek = this.now_day_month.getDay();//
		let _thisMothn_day = new Date(_thisYear,_thisMothn,1) ; ///
		let _thisDayDate = new Date(_thisYear,_thisMothn,_thisDay);
		let months = [31,this.monthDay(_thisYear,_thisMothn),31,30,31,30,31,31,30,31,30,31];
		let _thisMothn_lastDay = new Date(_thisYear,_thisMothn,months[_thisMothn]) ; ///
		let dateArray = [];//
		let llineDate = [];//
		let j=1;
		let ishs = true //
		for(let i=1 ;i <8;i++){
			let tdy = new Date(_thisYear, _thisMothn, i - 6 - _thisMothn_day.getDay()); //
			if(i==7){
				
				let tf =  tdy.getTime()>= _thisMothn_day.getTime() && tdy.getTime() <= _thisMothn_lastDay.getTime() ?true:false;
				if(!tf){
					ishs = false;
					break;
				}
			}
		}
		for(let i=1 ;i <43;i++){
			var Day = null;
			if(!ishs){
				Day = new Date(_thisYear, _thisMothn, i + 1 - _thisMothn_day.getDay()); //
			}else{
				Day = new Date(_thisYear, _thisMothn, i - 6 - _thisMothn_day.getDay()); //
			}
			dateArray.push({
				year:Day.getFullYear(),//
				month:Day.getMonth()+1,//1-12
				week:week[Day.getDay()],//1~7
				week_text:text_week[Day.getDay()],//
				day:Day.getDate(),//
				prevMoth: Day.getTime() < _thisMothn_day.getTime() ?true:false,//
				nowMonth: Day.getTime()>= _thisMothn_day.getTime() && Day.getTime() <= _thisMothn_lastDay.getTime() ?true:false,//
				nowDay:Day.getTime() == _thisDayDate.getTime() ?true:false,//
				nowYear:0,//
				nextMoth: Day.getTime() > _thisMothn_lastDay.getTime()?true:false,//
				beginEnd: Day.getTime() >= this.start_time.getTime() && Day.getTime() <= this.end_time.getTime()?true:false,//
				nongli:this.nongli(Day.getFullYear(),Day.getMonth()+1,Day.getDate()) 
			})
			
		}
		
		return dateArray;
	}
