

/**
 * 
 */
function email(value) {
	return /\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/.test(value);
}

/**
 * 
 */
function mobile(value) {
	return /^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/.test(value)
}

/**
 * URL
 */
function url(value) {
	return /^https?:\/\/(([a-zA-Z0-9_-])+(\.)?)*(:\d+)?(\/((\.)?(\?)?=?&?[a-zA-Z0-9_-](\?)?)*)*$/i.test(value)
}

/**
 * 
 */
function date(value) {
	return /^[1-2][0-9][0-9][0-9]-[0-1]{0,1}[0-9]-[0-3]{0,1}[0-9]$/.test(value)
}

/**
 * 
 */
function idCard(value) {
	return /^(^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$)|(^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])((\d{4})|\d{3}[Xx])$)$/.test(
		value)
}

/**
 * 
 */
function carNo(value) {
	// 
	const xreg = /^[A-Z]{1}[A-Z]{1}(([0-9]{5}[DF]$)|([DF][A-HJ-NP-Z0-9][0-9]{4}$))/;
	// 
	const creg = /^[A-Z]{1}[A-Z]{1}[A-HJ-NP-Z0-9]{4}[A-HJ-NP-Z0-9]{1}$/;
	if (value.length === 7) {
		return creg.test(value);
	} else if (value.length === 8) {
		return xreg.test(value);
	} else {
		return false;
	}
}

/**
 * 
 */
function chinese(value) {
	let reg = /^[\u4e00-\u9fa5]+$/gi;
	return reg.test(value);
}

/**
 * 
 */
function letter(value) {
	return /^[a-zA-Z]*$/.test(value);
}

/**
 * 
 */
function enOrNum(value) {
	//
	let reg = /^[0-9a-zA-Z]*$/g;
	return reg.test(value);
}

/**
 * json
 */
function jsonString(value) {
	if (typeof value == 'string') {
		try {
			var obj = JSON.parse(value);
			if (typeof obj == 'object' && obj) {
				return true;
			} else {
				return false;
			}
		} catch (e) {
			return false;
		}
	}
	return false;
}


/**
 * 
 */
function object(value) {
	return Object.prototype.toString.call(value) === '[object Object]';
}

/**
 *  truefalse
 * @param {Object} obj 
 * @param {Array<String>} filter  
 */
function checkObject(obj,filter=[]){
	let istrue = true;
	function testObjec(obj){
		if(typeof obj !=='object' || Array.isArray(obj) || obj==null ){
			istrue = false;
			return false;
		}
		for(let item in obj){
			if(filter.indexOf(item)===-1){
				if(typeof obj[item] ==='undefined' || obj[item]  === null ){
					
					istrue = false;
					break;
					return false;
				}
				if(typeof obj[item]  === 'object'){
					if(Array.isArray(obj[item])){
						if(obj[item].length==0){
							istrue = false;
							
							break;
							return false;
						}
					}else{
						testObjec(obj[item]);
						
					}
					
				}else if(typeof obj[item]  === 'string'){
					
					if(!uni.$tm.trim(obj[item])){
						istrue = false;
						break;
						return false;
					}
				}
			}
			
		}
		
	}
	testObjec(obj);
	return istrue;
}
//
function chinaPost(value) {
	return /[1-9]\d{5}(?!\d)/.test(value)
}
//
function bankCard(value) {
	let p = /^([1-9]{1})(\d{15}|\d{16}|\d{18})$/;
	let str = value.replace(/\s+/g, "");
	return p.test(str);
}
//
//6
function password(value,len=6) {
	let p = new RegExp(`^[\w+|\-|+\*\.\`!@#\$%\^\&\(\)\_\+\,\///]{`+len+`,}$`)
	return p.test(value);
}
//
//6
//1
function password2(value,len=6) {
	let p = new RegExp(`^[\w+|\-|+\*\.\`!@#\$%\^\&\(\)\_\+\,\///]{`+len+`,}$`)
	if(!p.test(value)) return false;
	if(!/[a-z]{1,}/.test(value)) return false;
	if(!/[A-Z]{1,}/.test(value)) return false;
	if(!/[0-9]{1,}/.test(value)) return false;
	return true;
}
            
export default {
	email,
	mobile,
	url,
	date,
	idCard,
	carNo,
	chinese,
	letter,
	enOrNum,
	jsonString,
	object,
	checkObject,
	chinaPost,
	password,
	password2,
}
