/**
 * cookie
 * @param {String} key 
 * @param {String} data 
 * @returns Boolean
 */
function setCookie(key, data) {
	try {
		uni.setStorageSync(key, data);
		return true;
	} catch (e) {
		return false;
	}
}
/**
 * cookie
 * @param {String} key 
 * @returns Boolean
 */
function delCookie(key) {
	try {
		uni.removeStorageSync(key);
		return true;
	} catch (e) {
		return false;
	}
}

/**
 * cookie
 * string
 * @param {String} key 
 * @returns json/string
 */
function getCookie(key) {
	try {
		const value = uni.getStorageSync(key);
		try {
			let val = JSON.parse(value)
			return val;
		} catch (e) {
			return value;
		}
	} catch (e) {
		return undefined;
	}
}


/**
 * 
 * @param {string} uri 
 * @param {string} key 
 * @param {string} value 
 * @returns 
 */
function httpUrlAddKey(uri, key, value) {
	if (!value) {
		return uri;
	}
	var re = new RegExp("([?&])" + key + "=.*?(&|$)", "i");
	var separator = uri.indexOf("?") !== -1 ? "&" : "?";
	if (uri.match(re)) {
		return uri.replace(re, "$1" + key + "=" + value + "$2");
	} else {
		return uri + separator + key + "=" + value;
	}
}

