import guid from './guid';
/**
 * 
 * tmzdy
 * 20217289:14:53
 * zhongjihan@sina.com
 * @param {Function} chooesefile -- 
 * @param {Function} selected -- 
 * @param {Function} addfile -- 
 * @param {Function} progress -- 
 * @param {Function} fail -- 
 * @param {Function} success -- 
 * @param {Function} complete -- 
 * @param {Function} start -- 
 * @param {Function} stop -- 
 */
class uploadfile {
	filelist = [];
	isStop = false;
	index = 0;
	constructor({maxfile,uploadUrl,opts,responseStu,file_list,isAuto}) {
		let arg = {
			maxfile:9,
			uploadUrl:'',
			file_list:[],
			isAuto:true,
			opts:{},
			maxsize:10*1024*1024,
			code:0,//
			type:'image',//
			extension:['*'],//
			responseStu:{
						code:'code',//
						data:'data',// 
						msg:'msg'//
					},
			...(arguments[0]??{})};
		let ots = {
				name:'file',header:{}
			}//{name: 'file', // keyfile,header: {}, // }
	    this.config={
			maxfile:arg.maxfile,
			uploadUrl:arg.uploadUrl,
			opts:{...ots,...arg.opts},
			file_list:arg.file_list,//.
			maxsize:arg.maxsize,
			code:arg.code,
			isAuto:arg.isAuto,//
			type:arg.type,//
			extension:arg.extension,//
			responseStu:{...arg.responseStu,...(responseStu||{})}
		}
	}
	/**
	 * 
	 */
	async chooesefile(){
		let t = this;
		return new Promise((rs,rj)=>{
			
			uni.chooseImage({
				count:t.config.maxfile,
				type:t.config.type,
				extension:t.config.extension,
				fail: (e) => {
					console.error(e);
					uni.$tm.toast("");
					rj(e);
				},
				success: (res) => {
					console.log(res);
					if(res.tempFilePaths.length==0){
						uni.$tm.toast("");
						return;
					}
					console.log(res);
					let imgarray = res.tempFilePaths;
					let fielist = res.tempFiles;
					let jgsk = [];
					//01234
					imgarray.forEach((item,index)=>{
						let isMaxsize = fielist[index].size>t.config.maxsize?true:false;
						jgsk.push({
							url:item,
							status:isMaxsize?'':"",
							progress:isMaxsize?100:0,
							fileId:guid(),
							statusCode:isMaxsize?4:0,
							data:null,//
						})
					})
					t.filelist.push(...jgsk)
					
					t.selected(t.filelist);
					if(t.config.isAuto){
						t.start();
					}
					
					rs(t.filelist)
				}
			})
			
		})
	}
	async chooseMPH5weixinFile(){
		let t = this;
		return new Promise((rs,rj)=>{
			var fs = uni.chooseFile;

			fs = uni.chooseMessageFile;

			var config = {
				count:t.config.maxfile,
				type:t.config.type,
				extension:t.config.extension,
			}
			if(!t.config.extension||!Array.isArray(t.config.extension)||t.config.extension?.length==0){
				delete config.extension
			}
			fs({
				...config,
				fail: (e) => {
					console.error(e);
					uni.$tm.toast("");
					rj(e);
				},
				success: (res) => {
					if(res.tempFiles.length==0){
						uni.$tm.toast("");
						return;
					}
					let fielist = res.tempFiles;
					let jgsk = [];
					//01234
					fielist.forEach((item,index)=>{
						let isMaxsize = fielist[index].size>t.config.maxsize?true:false;
						let ftype = item.name||""
						if(ftype){
							ftype = ftype.substr(ftype.lastIndexOf(".")+1).toLocaleLowerCase();
						}
						jgsk.push({
							url:item.path,
							name:item.name||'',
							type:ftype,
							status:isMaxsize?'':"",
							progress:isMaxsize?100:0,
							fileId:guid(),
							statusCode:isMaxsize?4:0,
							data:null,//
						})
					})
					t.filelist.push(...jgsk)
					
					t.selected(t.filelist);
					if(t.config.isAuto){
						t.start();
					}
					
					rs(t.filelist)
				}
			})
			
		})
	}
	setConfig({maxfile,uploadUrl,opts,file_list,isAuto,responseStu}){
		let arg = arguments.length==0?{}:arguments[0];
		this.config={...this.config,...arg}
	}
	// 
	/**
	 * 
	 * @param {Object} filelist
	 */
	addfile(filelist){
		if(typeof filelist !=='object'&&!Array.isArray(filelist)) return;
		this.filelist.push(...filelist)
	}
	// 
	selected(filelist){}
	// 
	progress(item){}
	// 
	fail(item){}
	// 
	success(item){}
	// 
	complete (filelist){}
	// 
	start(){
		if(this.filelist.length<=0){
			uni.$tm.toast("");
			return;
		}
		let t = this;
		// t
		this.index = 0;
		this.isStop = false;
		function startupload(){
			if(t.isStop) return;
			let item = t.filelist[t.index];
			if(!item || typeof item === 'undefined'){
				// 
				t.complete(t.filelist);
				
				return;
			}
			
			if(item.statusCode==3||item.statusCode==1||item.statusCode==4){
				// 
				t.index++;
				startupload();
				return;
			}
			
			const upObj = uni.uploadFile({
				url:t.config.uploadUrl,
				name:t.config.opts?.name??'file',
				header:t.config.opts?.header??{},
				filePath:item.url,
				formData:{file_name:item.name},
				success:(res)=>{
					if(res.statusCode !=200){
						item.statusCode = 2;
						item.status = "";
						uni.$tm.toast(String(res.statusCode))
						t.fail(item)
						t.index++;
						return;
					}
					let jsd={};
					let isOk = true;
					// jsonfaslestring.
					let isJsonCallbackData = true;
					
					try{
						jsd = JSON.parse(res.data);
					}catch(e){
						isJsonCallbackData=false;
						jsd = res.data;
						item.data = res.data;
					}
					
					if(isJsonCallbackData){
						try{
							item.data = jsd[t.config.responseStu.data];
							if(typeof item.data == 'object'){
								item.data['name'] = item.name;
								item.data['id'] = item['id']||"";
							}
							let itecode = jsd[t.config.responseStu.code]
							if(itecode!==t.config.code){
								isOk = false;
							}
						}catch(e){
							isOk = false;
						}
					}
					
					
					if(!isOk){
						uni.$tm.toast(jsd[t.config.responseStu.msg]||"")
						item.statusCode = 2;
						item.status = "";
						t.fail(item)
						t.index++;
						return;
					}
					
					// 
					item.statusCode = 3;
					item.status = "";
					uni.$tm.toast("")
					// t.filelist[t.index] = item;
					// t.filelist.splice(t.index,1,item)
					
					t.success(item)
					
				},
				fail:(res)=>{
					
					uni.$tm.toast(res.errMsg)
					item.statusCode = 2;
					item.status = "";
					// t.filelist[t.index] = item;
					t.fail(item)
					t.index++;
				},
				complete:(res)=>{
					// 
					
					startupload();
				}
			})
			if(upObj){
				upObj.onProgressUpdate((res)=>{
					t.filelist[t.index].statusCode = 1;
					t.filelist[t.index].status = "";
					t.filelist[t.index].progress = res.progress;
					// t.filelist[t.index] = item;
					t.progress(item)
					
				})
			}
			
		}
		startupload();
	}
	// 
	stop(){
		this.isStop = true;
	}
	
}

/**
 * 
 * tmzdy
 * 20217289:14:53
 * zhongjihan@sina.com
 * 
 * @param {Number} maxfile  9 ;
 * @param {String} uploadUrl -- ""
 * @param {Object} opts -- {}
 * @param {Function} progress  {} --
 * @param {Function} success  {} --
 * @param {Function} selected  {} --
 * @param {Function} fail  {} --
 * @param {Function} complete  {} -- 
 */
function chooseImgUpload(maxfile=9,uploadUrl="",opts={},progress,success,selected,fail,complete){
	uni.chooseImage({
		count:maxfile,
		fail: (e) => {
			uni.$tm.toast("");
		},
		success: (res) => {
			
			if(res.tempFilePaths.length==0){
				uni.$tm.toast("");
				return;
			}
			let imgarray = res.tempFilePaths;
			let jgsk = [];
			//0123
			imgarray.forEach((item,index)=>{
				jgsk.push({
					url:item,
					status:"",
					progress:0,
					fileId:guid(),
					statusCode:0,
					data:null,//
				})
			})
			if(selected){
				selected(jgsk);
			}
			
			let index = 0;
			function startupload(){
				let item = jgsk[index];
				if(!item){
					// 
					if(complete){
						complete(jgsk);
					}
					return;
				}
				if(item.statusCode==2||item.statusCode==1){
					// 
					index++;
					startupload();
				}
				
				const upObj = uni.uploadFile({
					url:uploadUrl,
					name:opts?.name??'file',
					header:opts?.header??{},
					filePath:item.url,
					success:(res)=>{
						if(res.statusCode !=200){
							item.statusCode = 2;
							item.status = "";
							uni.$tm.toast(res.errMsg)
							if(fail){
								fail(item)
							}
							return;
						}
						try{
							item.data = JSON.parse(res.data).data;
						}catch(e){
							item.statusCode = 2;
							item.status = "";
							uni.$tm.toast(res.errMsg)
							if(fail){
								fail(item)
							}
							return;
						}
						
						// 
						item.statusCode = 3;
						item.status = "";
						item.data = JSON.parse(res.data).data;
						uni.$tm.toast("")
						if(success){
							success(item)
						}
						
					},
					fail:(res)=>{
						
						uni.$tm.toast(res.errMsg)
						item.statusCode = 2;
						item.status = "";
						if(fail){
							fail(item)
						}
					},
					complete:(res)=>{
						// 
						index++;
						startupload();
					}
				})
				if(upObj){
					upObj.onProgressUpdate((res)=>{
						item.statusCode = 1;
						item.status = "";
						item.progress = res.progress
						if(progress){
							progress(item)
						}
						
					})
				}
				
			}
			startupload();
		}
	})
}

export default {
	chooseImgUpload,uploadfile
}